import {Inject, Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {environment} from '../../../../../../../environments/environment';
import {Observable} from 'rxjs';
import {MainPage} from '../../../../../main/main.page';
import {ReportService} from '../../../../report.service';

@Injectable({
  providedIn: 'root'
})
export class Sheet423Service {

  sheetName = 'mdfir423/';

  // @ts-ignore
  private reportSelectedDate: string;
  constructor(private http: HttpClient, private reportService: ReportService) {
    this.reportService.selectedDate.subscribe((date) => {
      this.reportSelectedDate = date;
    });
  }

  // tslint:disable-next-line:typedef
  postMDFIR423Data(data: any): Observable<any>{
    return this.http.post<any>(`${environment.baseApi}/` + this.sheetName + this.reportSelectedDate, data);
  }

  getMDFIR423Data(): Observable<any>{
    return this.http.get<any>(`${environment.baseApi}/` + this.sheetName +  this.reportSelectedDate);
  }

  putMDFIR423Data(id: any, data: any): Observable<any> {
    return this.http.put<any>(`${environment.baseApi}/` + this.sheetName +  this.reportSelectedDate + '/' + id, data);
  }

  // tslint:disable-next-line:typedef
  deleteMDFIR423Data(id: any) {
    return this.http.delete<any>(`${environment.baseApi}/` + this.sheetName + this.reportSelectedDate + '/' + id);
  }
}
