import {Component, Inject, OnDestroy, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Sheet423Service} from '../sheet423.service';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {resolveSrv} from 'dns';
import { NotifierService } from 'src/app/notifier.service';


@Component({
  selector: 'app-dialog',
  templateUrl: './dialog.component.html',
  styleUrls: ['./dialog.component.css']
})
export class DialogComponent implements OnInit, OnDestroy {
  picker1: any;
  picker2: any;
  mdfir423Form: FormGroup;
  actionsBtn = 'Save';

  constructor(private formBuilder: FormBuilder,
              private sheet423Service: Sheet423Service,
              private notifierService: NotifierService,
              @Inject(MAT_DIALOG_DATA) public editData: any,
              // tslint:disable-next-line:no-shadowed-variable
              private MatDialogRef: MatDialogRef<DialogComponent>) { }

  ngOnInit(): void {
    this.mdfir423Form = this.formBuilder.group({
      code: ['', Validators.required],
      item_description: ['', Validators.required],
      amount: ['', Validators.required],

    });
    if (this.editData){
      this.actionsBtn = 'Update';
      // tslint:disable-next-line:no-unused-expression
      this.mdfir423Form.controls.code.setValue(this.editData.code);
      this.mdfir423Form.controls.item_description.setValue(this.editData.item_description);
      this.mdfir423Form.controls.amount.setValue(this.editData.amount);
    }
  }

  ngOnDestroy(): void {
  }

  addDetails(): void {
    if (!this.editData){
      if (this.mdfir423Form.valid) {
        this.sheet423Service.postMDFIR423Data(this.mdfir423Form.value).subscribe(
          {
            next: (res) => {
              // alert('Mdfir423 added');
              this.notifierService.showNotification('MDFIR423 updated successfully', 'OK', 'success');
              this.mdfir423Form.reset();
              this.MatDialogRef.close('save');
            },
            error: () => {
              // alert('MDFIRprobws');
              this.notifierService.showNotification('Unable to add new row', 'OK', 'error');
            }}
        );
      }
    } else {
      this.putDetails();
  }
    // tslint:disable-next-line:typedef
    console.log(this.mdfir423Form.value);
}

  // tslint:disable-next-line:typedef
  private putDetails(): void {
    this.sheet423Service.putMDFIR423Data(this.editData.id, this.mdfir423Form.value).subscribe({
      next: (response) => {
        // alert('MDFIR423 updated successfully');
        this.notifierService.showNotification('MDFIR423 updated successfully', 'ok', 'success');
        this.mdfir423Form.reset();
        this.MatDialogRef.close('update');
      },
      error: () => {
        // alert('Error encountered');
        this.notifierService.showNotification('Error updating Report', 'OK', 'error');
      }
    });
  }
  }

